// =================================
// Блок отзывов reviews
// =================================
.reviews {
  width: 100%;
  height: 812px;
  background-image: url(../img/mountain3--320.jpg);
  background-size: cover;

  @media (min-width: $screen-sm) {
    height: 1570px;
    background-image: url(../img/mountain3--768.jpg);
  }

  @media (min-width: $screen-lg) {
    height: 1623px;
    background-image: url(../img/mountain3--1200.jpg);
  }

  &__container {
    @include content-area();
    padding-top: 215px;

    @media (min-width: $screen-sm) {padding-top: 930px; }

    @media (min-width: $screen-lg) {padding-top: 961px; }
  }

  &__wrapper {
    height: 558px;
    background-color: rgba(226,226,227,.84);
    box-sizing: border-box;

    @media (min-width: $screen-lg) {height: 569px; }
  }

  &__title {
    @include title();
    padding-top: 43px;
  }

  &__slick-wrap {
    margin-top: 40px;
    width: $screen-xs-content;
    box-sizing: border-box;

    @media (min-width: $screen-sm) {width: $screen-sm-content; }

    @media (min-width: $screen-lg) {width: $screen-lg-content; }
  }
}
