.trace {
  height: 529px;
  background-size: cover;

  @media (min-width: $screen-xs) {
    background-image: url(../img/mountain2--320.jpg);
  }

  @media (min-width: $screen-sm) {
    height: 831px;
    background-image: url(../img/mountain2--768.jpg);
  }

  @media (min-width: $screen-lg) {
    height: 867px;
    background-image: url(../img/mountain2--1200.jpg);
  }

  &__container {
    @include content-area();
  }

  &__title {
    @include title();
    padding-top: 3px;

    @media (min-width: $screen-lg) {padding-top: 20px; }
  }

  &__slick-wrap {
    margin-top: 55px;
    width: $screen-xs-content;
    box-sizing: border-box;

    @media (min-width: $screen-sm) {
      margin-top: 68px;
      width: $screen-sm-content;
    }

    @media (min-width: $screen-lg) {
      margin-top: 55px;
      width: $screen-lg-content;
    }
  }
}
